<template>
  <div class="box">
    <div class="amap-page-container">
      <div :style="{width:'100%',height:'500px'}">
        <el-amap vid="amap" disabled="false" :plugin="plugin" class="amap-demo" :center="center">
          <el-amap-marker vid="marker" :position="center" :label="label"></el-amap-marker>
          <el-amap-circle
            vid="circle"
            :center="center"
            :radius="radius"
            fill-opacity="0.2"
            strokeColor="#38f"
            strokeOpacity="0.8"
            strokeWeight="1"
            fillColor="#38f"
          ></el-amap-circle>
        </el-amap>
      </div>
    </div>
    <!-- <button @click="demo">测试</button> -->
    <!-- <div>--------------</div>
    <div class="block" v-for="fit in fits" :key="fit">
      <span class="demonstration">{{ fit }}</span>
      <el-image style="width: 100px; height: 100px" :src="url" :fit="fit"></el-image>
    </div>
    <div>----------------</div>-->
    <div class="spase"></div>
    <h2>饮品补给站</h2>
    <el-divider>
      <i class="el-icon-cold-drink" style="font-size:30px;color:green"></i>
    </el-divider>
    <el-row class="row">
      <el-col :span="3" v-for="(item, index) in drinkList" :key="index">
        <el-card :body-style="{ padding: '0px' }">
          <div class="demo-image__preview">
            <el-tooltip class="item" effect="dark" :content="item.name" placement="top-start">
              <el-image :src="item.img" class="image" :preview-src-list="url" lazy></el-image>
            </el-tooltip>
          </div>
          <div style="padding: 14px;">
            <span>{{item.name}}</span>
            <br />
            <span style="color:red">{{'￥'+item.price}}</span>
            <el-rate
              v-model="item.dengji"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            ></el-rate>
            <div class="bottom clearfix">
              <div>
                <el-tooltip
                  class="item"
                  effect="dark"
                  :content="item.content"
                  placement="top-start"
                >
                  <span class="content">{{item.content}}</span>
                </el-tooltip>
              </div>
              <!-- <time class="time">{{ currentDate }}</time> -->
              <el-input-number v-model="item.num" :min="0" :max="10"></el-input-number>
              <el-button type="danger" class="button" @click="drinkBuy(item.id)">立即购买</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    const self = this;

    return {
      // 地图提示文字
      label: {
        content: "钦汇园",
        offset: [10, 12]
      },
      // 增加地图区域框框
      radius: 100,
      center: [121.59996, 31.197646],
      lng: 0,
      lat: 0,
      loaded: false,
      plugin: [
        {
          size: 1300 * 1300,
          enableHighAccuracy: true, //是否使用高精度定位，默认:true
          timeout: 100, //超过10秒后停止定位，默认：无穷大
          maximumAge: 0, //定位结果缓存0毫秒，默认：0
          convert: true, //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true, //显示定位按钮，默认：true
          buttonPosition: "RB", //定位按钮停靠位置，默认：'LB'，左下角
          showMarker: true, //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: true, //定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true, //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true, //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
          extensions: "all",
          pName: "Geolocation",
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                console.log(result);
                if (result && result.position) {
                  self.lng = result.position.lng;
                  self.lat = result.position.lat;
                  self.center = [self.lng, self.lat];
                  self.loaded = true;
                  self.$nextTick();
                }
              });
            }
          }
        }
      ],
      // ---------------------------
      allPrice: 0,
      drinkList: [],
      fits: ["fill", "contain", "cover", "none", "scale-down"],
      url: [
        "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
      ]
    };
  },
  methods: {
    // 地图范围
    addRadius() {
      this.radius += 10;
    },
    drinkBuy(id) {
      let choose = this.drinkList.filter(item => {
        return item.id == id;
      });
      this.allPrice = choose[0].price * choose[0].num;
    },
    demo() {
      this.$http.get("test", { params: { name: "lisi" } }).then(res => {});
    },
    async getMenu() {
      const res = await this.$http.get("breakfastmenu");
      this.drinkList = res.data;
    }
  },
  created() {
    this.getMenu();
  }
};
</script>
<style lang="stylus" scoped>
/deep/.el-input-number__increase, /deep/.el-input-number__decrease {
  width: 20px;
}

h2 {
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 50px;
  font-size: 30px;
  color: #4cae4c;
}

.spase {
  margin-top: 10px;
  border-top: 20px solid #9e9e9e45;
}

/deep/img.el-image__inner.el-image__preview {
  height: 200px;
}

.drink {
  height: 350px;
  width: 15%;
  box-shadow: 0px 0px 5px 0px #9a9999;
  margin-left: 50px;
  margin-right: 50px;
}

.box {
  width: 100%;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  text-align: center;
  font-size: 10px;
  width: 80px;
  height: 40px;
  float: right;
}

.image {
  width: 80%;
  display: block;
  margin-left: 10%;
}

.clearfix:before, .clearfix:after {
  display: table;
  content: '';
}

.clearfix:after {
  clear: both;
}

.el-col.el-col-3 {
  margin-left: 20px;
  margin-bottom: 20px;
}

.el-col-3 {
  width: 18%;
}

.content {
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
  width: 260px;
  height: 24px;
  display: block;
}

.row.el-row {
  margin-left: 30px;
}

/deep/.el-input-number .el-input__inner {
  -webkit-appearance: none;
  padding-left: 0px;
  padding-right: 0px;
  text-align: center;
}

.el-input-number {
  width: 145px;
}

.el-rate {
  display: inline-block;
  width: 140px;
  height: 20px;
  line-height: 1;
  margin-left: 40px;
}

.el-divider.el-divider--horizontal {
  width: 94%;
  margin-left: 3%;
  background-color: #4cae4c;
}
</style>