<template>
  <div class="box">
    <button @click="demo">测试</button>
    <!-- <div>--------------</div>
    <div class="block" v-for="fit in fits" :key="fit">
      <span class="demonstration">{{ fit }}</span>
      <el-image style="width: 100px; height: 100px" :src="url" :fit="fit"></el-image>
    </div>
    <div>----------------</div>-->
    <div class="spase"></div>
    <h2>饮品补给站</h2>
    <el-divider>
      <i class="el-icon-cold-drink" style="font-size:30px;color:green"></i>
    </el-divider>
    <el-row class="row">
      <el-col :span="3" v-for="(item, index) in drinkList" :key="index">
        <el-card :body-style="{ padding: '0px' }">
          <div class="demo-image__preview">
            <el-tooltip class="item" effect="dark" :content="item.name" placement="top-start">
              <el-image :src="item.img" class="image" :preview-src-list="url" lazy></el-image>
            </el-tooltip>
          </div>
          <div style="padding: 14px;">
            <span>{{item.name}}</span>
            <br />
            <span style="color:red">{{'￥'+item.price}}</span>
            <el-rate
              v-model="item.dengji"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            ></el-rate>
            <div class="bottom clearfix">
              <div>
                <el-tooltip
                  class="item"
                  effect="dark"
                  :content="item.content"
                  placement="top-start"
                >
                  <span class="content">{{item.content}}</span>
                </el-tooltip>
              </div>
              <!-- <time class="time">{{ currentDate }}</time> -->
              <el-input-number v-model="item.num" :min="0" :max="10"></el-input-number>
              <el-button type="danger" class="button" @click="drinkBuy(item.id)">立即购买</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- <div class="row">
      <div class="drink">
        <img src="../assets/img/drink5.jpg" alt />
        <div>
          <span>ll</span>
        </div>
      </div>
